<template>
  <section v-if="answers" class="panel">
    <answer-list-element
    v-for="(answer, index) in answers"
    :key="answer"
    :answer="answer"
    @click="onClick(index)"></answer-list-element>
  </section>
</template>

<script>
import AnswerListElement from './answer-list-element';

export default {
  name: "AnswerList",
  components: {
    AnswerListElement
  },
  props: {
    answers:Array
  },
  methods: {
    onClick(answerIndex) {
      this.$emit("SelectAnswer");
    }
  }
}
</script>




